/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: end;
}

.w-100 {
  width: 100%;
}

.boton-azul {
  background-color: #00557d;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  text-transform: uppercase;
  border: none;
}
.boton-azul:hover {
  background-color: rgb(0, 72.25, 106.25);
  cursor: pointer;
}

.boton-azul-block {
  background-color: #00557d;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  text-transform: uppercase;
  border: none;
}
.boton-azul-block:hover {
  background-color: rgb(0, 72.25, 106.25);
  cursor: pointer;
}

.boton-verde {
  background-color: #008672;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  text-transform: uppercase;
  border: none;
}
.boton-verde:hover {
  background-color: rgb(0, 113.9, 96.9);
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #db4444;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  text-transform: uppercase;
  border: none;
  width: 100%;
  line-height: 1.8;
}
.boton-rojo-block:hover {
  background-color: rgb(204.5679372197, 39.3820627803, 39.3820627803);
  cursor: pointer;
}

.boton-verde-block {
  background-color: #58b63b;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  text-transform: uppercase;
  border: none;
}
.boton-verde-block:hover {
  background-color: rgb(74.8, 154.7, 50.15);
  cursor: pointer;
}

.dark-mode-body {
  background-color: rgb(30.6, 30.6, 30.6);
}
.dark-mode-body p,
.dark-mode-body a,
.dark-mode-body h1,
.dark-mode-body h2,
.dark-mode-body h3,
.dark-mode-body h4 {
  color: white;
}
.dark-mode-body .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode-body .precio {
  color: rgb(0, 171.6, 145.9880597015);
}
.dark-mode-body .iconos-caracteristicas img {
  filter: invert(100);
}
.dark-mode-body .formulario label,
.dark-mode-body .formulario legend,
.dark-mode-body .formulario p {
  color: #e1e1e1;
}
.dark-mode-body .formulario input:not([type=submit]),
.dark-mode-body .formulario textarea,
.dark-mode-body .formulario select {
  background-color: rgb(187, 187, 187);
}

/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg);
  background-color: rgba(25, 50, 65, 0.575);
  background-blend-mode: darken;
}

.webp .inicio {
  background-image: url(../img/header.webp);
  background-color: rgba(25, 50, 65, 0.575);
  background-blend-mode: darken;
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: center;
  color: #ffffff;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    text-align: left;
  }
}

.barra {
  display: flex;
  align-items: center;
}
.barra .logo-header {
  max-width: 100%;
  width: 24rem;
  height: auto;
}
@media (min-width: 768px) {
  .barra .logo-header {
    width: 28rem;
  }
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyrigth {
  color: #ffffff;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
  filter: invert(100);
}
.derecha .dark-mode:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  display: none;
  visibility: hidden;
  opacity: 0;
  transition: visibility, 0.3s ease-in-out, opacity 0.3s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #ffffff;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: rgb(0, 190.4, 161.9820895522);
  transition: ease-in-out 0.5 all;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.contenedor-menu {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.mobile-menu {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .mobile-menu {
    visibility: hidden;
  }
}
.mobile-menu img {
  width: 4rem;
}

.menu {
  display: flex;
  flex-direction: column;
}
.menu .line {
  background-color: aliceblue;
  width: 40px;
  height: 4px;
  margin: 5px 0;
}
.menu .line1 {
  transform-origin: 0% 0%;
  transition: transform 0.4s ease-in-out;
}
.menu .line2 {
  transition: transform 0.2s ease-in-out;
}
.menu .line3 {
  transform-origin: 0% 100%;
  transition: transform 0.4s ease-in-out;
}

.mostrar {
  display: flex;
  flex-direction: column;
  visibility: visible;
  opacity: 1;
}

.line-active-1 {
  transform: rotate(45deg);
}

.line-active-2 {
  transform: scaleY(0);
}

.line-active-3 {
  transform: rotate(-45deg);
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: rgb(234.6, 234.6, 234.6);
  border: 1px solid #999999;
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .anuncio {
    margin-top: 0;
  }
}
.anuncio .contenido-anuncio {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.anuncio h3 {
  margin: 0;
}
.anuncio .img-anuncio {
  width: 100%;
  height: 200px; /* Ajusta este valor seg√∫n tus necesidades */
  object-fit: cover; /* Recorta la imagen para llenar el √°rea */
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 5rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.precio {
  color: #008672;
  font-weight: bold;
  font-size: 3rem;
}

.no-web .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #ffffff;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #ffffff;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #008672;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.2rem;
  background-color: #008672;
  margin-top: 0.5rem;
}

.testimonial {
  background-color: #00557d;
  color: #ffffff;
  font-size: 2.4rem;
  padding: 2rem;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
  top: -2rem;
}
.testimonial p {
  text-align: right;
  color: #ffffff;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #ffffff;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
.formulario textarea {
  height: 15rem;
  resize: none;
}
.formulario .forma-contacto {
  display: flex;
  max-width: 30rem;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

/**Paginas internas**/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nostros blockquote {
  margin-top: 3rem 0 0 0;
  font-weight: 900;
  font-size: 2.2rem;
  padding: 1rem 0 3rem 0;
}

.dark-mode-body blockquote {
  color: white;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #ffffff;
  margin-bottom: 1rem;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #db4444;
}
.alerta.exito {
  background-color: rgb(79.2, 163.8, 53.1);
}

table.propiedades {
  margin-top: 4rem;
  width: auto;
  border-spacing: 0;
  color: #ffffff;
}
@media (min-width: 768px) {
  table.propiedades {
    width: 100%;
  }
}
table.propiedades thead {
  background-color: #00557d;
}
table.propiedades thead th {
  margin: auto;
  color: #ffffff;
  padding: 1rem;
}
table.propiedades tbody tr {
  text-align: center;
  align-content: center;
}
table.propiedades tbody tr td .img-tabla {
  display: flex;
  margin: auto;
}
table.propiedades .img-tabla {
  width: 10rem;
  height: auto;
}
@media (min-width: 768px) {
  table.propiedades .img-tabla {
    width: 15rem;
  }
}

.btnAccion {
  border-radius: 1rem;
  padding: 1rem;
}

.img-anuncio {
  width: 20rem;
}