@use "base/variables" as v;
@use "base/mixins" as m;

.formulario {
  p {
    font-size: 1.4rem;
    color: v.$grisOscuro;
    margin: 2rem 0;
  }

  legend {
    font-size: 2rem;
    color: v.$grisOscuro;
  }

  label {
    font-weight: v.$bold;
    text-transform: uppercase;
    display: block;
  }

  input:not([type="submit"]),
  textarea,
  select {
    padding: 1rem;
    display: block;
    width: 100%;
    background-color: v.$blanco;
    border: 1px solid v.$gris;
    border-radius: 1rem;
    margin-bottom: 1rem;
  }

  textarea {
    height: 15rem;
    resize: none;
  }

  .forma-contacto {
    display: flex;
    max-width: 30rem;
    justify-content: space-between;
    align-items: center;
  }

  input[type="radio"] {
    margin: 0;
    width: auto;
  }
}
