@use "../base/variables" as v;
@use "../base/mixins" as m;

.contenido-nosotros {
  @include m.tablet {
    @include m.grid(2, 2rem);
  }
}

.texto-nostros {
  blockquote {
    margin-top: 3rem 0 0 0;
    font-weight: v.$black;
    font-size: 2.2rem;
    padding: 1rem 0 3rem 0;
  }
}

.dark-mode-body {
  blockquote {
    color: white;
  }
}
